@font-face {
  font-family: "BebasNue";
  src: url("../../fonts//BebasNeue-Regular.woff2") format("woff2"), url("../../fonts/BebasNeue-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.publications-content {
  position: relative;
  padding: 3rem 5rem;
  margin: 0px auto;
  max-width: 1440px;
}
@media (min-width: 0) and (max-width: 768px) {
  .publications-content {
    padding: 3rem 1rem;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .publications-content {
    justify-content: flex-start;
  }
}
.publications-content .views-row {
  border-top: 1px solid rgb(109, 109, 109);
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem 2.5rem;
}
@media (min-width: 0) and (max-width: 768px) {
  .publications-content .views-row {
    flex-direction: column;
    justify-content: center;
    padding: 4rem 2rem;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .publications-content .views-row {
    flex-direction: column;
    justify-content: center;
    padding: 2rem 3rem;
  }
}
@media (min-width: 1025px) {
  .publications-content .views-row {
    flex-direction: row;
  }
}
.publications-content .views-row .views-field-field-coverimage .field-content .paragraph--type--publication-image {
  display: block;
  flex: none;
  margin-right: 30px;
  margin-bottom: 0px;
}
@media (min-width: 0) and (max-width: 768px) {
  .publications-content .views-row .views-field-field-coverimage .field-content .paragraph--type--publication-image {
    margin-right: 0px;
    margin-bottom: 20px;
  }
}
.publications-content .views-row .views-field-field-coverimage .field-content .paragraph--type--publication-image div img {
  width: 350px;
  height: 100%;
}
@media (min-width: 0) and (max-width: 768px) {
  .publications-content .views-row .views-field-field-coverimage .field-content .paragraph--type--publication-image div img {
    width: 100%;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .publications-content .views-row .views-field-field-coverimage .field-content .paragraph--type--publication-image div img {
    width: 350px;
  }
}
.publications-content .views-row .views-field-field-contentbody .field-content .paragraph--type--publication .category {
  margin-bottom: 1rem;
}
.publications-content .views-row .views-field-field-contentbody .field-content .paragraph--type--publication .category div a {
  text-decoration: none;
  color: rgb(44, 44, 44);
  font-size: 0.9rem;
}
.publications-content .views-row .views-field-field-contentbody .field-content .paragraph--type--publication .title div {
  font-size: 1.1rem;
  text-decoration: underline;
  color: rgb(96, 96, 237);
  text-underline-offset: 0.3rem;
  margin-bottom: 0.8rem;
  cursor: pointer;
}
.publications-content .views-row .views-field-field-contentbody .field-content .paragraph--type--publication .author div {
  font-size: 0.7rem;
  color: rgb(44, 44, 44);
  margin-bottom: 1rem;
}
.publications-content .views-row .views-field-field-contentbody .field-content .paragraph--type--publication .body div {
  max-width: 90%;
  font-size: 0.9rem;
  color: rgb(44, 44, 44);
  margin-bottom: 1rem;
  font-family: sans-serif;
}
.publications-content .views-row .views-field-field-contentbody .field-content .paragraph--type--publication .language div {
  display: flex;
  color: rgb(96, 96, 237);
  gap: 0.5rem;
  align-items: center;
  text-decoration: underline;
  text-underline-offset: 0.2rem;
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
}
.publications-content .views-row .views-field-field-contentbody .field-content .paragraph--type--publication .document div {
  display: flex;
  color: rgb(96, 96, 237);
  gap: 0.5rem;
  text-decoration: underline;
  text-underline-offset: 0.2rem;
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
}
.publications-content .views-row .views-field-field-publication-date {
  position: relative;
  min-width: 90px;
  font-weight: 600;
  font-size: 0.8rem;
  white-space: nowrap;
  display: flex;
  gap: 0.5rem;
}
@media (min-width: 0) and (max-width: 768px) {
  .publications-content .views-row .views-field-field-publication-date {
    width: 100%;
  }
}
@media (min-width: 768px) and (max-width: 1024px) {
  .publications-content .views-row .views-field-field-publication-date {
    width: 100%;
  }
}
.publications-content .views-row:nth-child(odd) {
  background-color: rgb(239, 239, 239);
}